@import '../layout.scss';

// The gray background
.backdrop {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;

  display: -webkit-box;

  display: -ms-flexbox;

  display: flex;

  padding: 50px;

  -webkit-transition: all 0.4s;

  transition: all 0.4s;

  opacity: 1;
  background-color: rgba(0,0,0,0.6);

  -webkit-box-pack: center;

      -ms-flex-pack: center;

          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

.backdrop.hidden {
  z-index: -1;

  display: block;

  opacity: 0;
}

// The modal "window"
.modal {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;

  max-width: 400px;
  min-height: 250px;
  margin: 0 auto;
  padding: 20px;

  opacity: 1;
  border-radius: 5px;
  background-color: #fff;

  font-size: 1.4rem;

  -ms-flex-pack: distribute;

      justify-content: space-around;
}

.modal.hidden {
	opacity: 0;
}

.title {
  margin: 0 auto;

  text-align: center;

  font-size: 1.6rem;
  font-weight: 600;
}

.error {
  text-align: justify;
}

.controls {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;

  -webkit-box-pack: center;

      -ms-flex-pack: center;

          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

@include phone-portrait-only() {
  .modal {
    font-size: 1.2rem;
  }

  .error {
    text-align: center;
  }
};



