@import '../layout.scss';

.wrapper {
  width: 32px;
  height: 32px;
}

.icon {
  cursor: pointer;
  -webkit-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
}

.icon.active {
  border: 3px solid black;
  border-radius: 16px;
}

.icon:hover {
  -webkit-transform: scale(1.3);
          transform: scale(1.3);
}

.icon.active:hover {
  -webkit-transform: scale(1);
          transform: scale(1);
}



